.blog-new-page-main-container-secondary {
  padding: 20px 110px;
}

.blog-new-page-main-container-secondary-main-image {
  width: 100%;
  height: 700px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.blog-new-page-main-container-secondary-main-image .banner-image {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
  user-select: none;
}

.blog-new-page-main-container-secondary-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  user-select: none;
}

.blog-new-page-main-container-secondary-content h1{
    margin-top: 20px;
    font-size: 45px;
}

.blog-new-page-main-container-secondary-content h2{
    margin-top: 20px;
    font-size: 42.5px;
}

.blog-new-page-main-container-secondary-content h3{
    margin-top: 20px;
    font-size: 40px;
}

.blog-new-page-main-container-secondary-content p{
    font-size: 18px;
    margin-top: 10px;
    line-height: 2;
}

.blog-new-page-main-container-secondary-content img {
  max-width: 500px;
  max-height: 450px;
  margin-top: 20px;
  /* Prevent the image from being selectable */
  user-select: none;
}

.share-this-post{
    border-top: 1px solid gray;
    border-bottom: 1px solid gray;
    margin-top: 40px;
    margin-bottom: 40px;
    padding: 25px 15px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.share-this-post-social-media{
    display: flex;
    flex-direction: row;
    gap: 7.5px;
}

.share-this-post p{
    font-size: larger;
    color: #612d91;
    font-weight: 700;
}